import { CatalogData, CategoryData } from "@/types";
import { getCatalogPath, findCatalogItem } from "@/utils/catalog-slug";

/**
 * Yagona breadcrumblar ro'yxatini tuzadi.
 *
 * @param catalogData - katalog ma'lumotlari
 * @param slug - asosiy yo'l slug
 * @param categorySlug - ixtiyoriy kategoriya slugi
 * @param includeBase - asosiy "Каталог" tugunini qo'shish kerakmi
 * @returns Breadcrumb obyektlar ro'yxati
 */
export const getBreadcrumbPaths = (
  catalogData: CatalogData[],
  slug?: string,
  categorySlug?: string,
  includeBase: boolean = false
) => {
  let breadcrumbs: {
    name: string;
    href?: string;
    catalogItem?: CatalogData;
  }[] = [];

  if (includeBase) {
    breadcrumbs.push({ name: "Каталог", href: "/catalog", catalogItem: undefined });
  }

  if (slug) {
    const catalogPath = getCatalogPath(catalogData, slug);
    const catalogCrumbs = catalogPath.map((item, index, arr) => ({
      name: item.title,
      href:
        index < arr.length - 1
          ? `/catalog/${arr.slice(0, index + 1).map((i) => i.slug).join("/")}`
          : undefined,
      catalogItem: item,
    }));
    breadcrumbs = [...breadcrumbs, ...catalogCrumbs];
  }

  if (slug && categorySlug) {
    const subcatalogItem = findCatalogItem(catalogData, slug);
    const categoryItem =
      subcatalogItem && subcatalogItem.categories
        ? (subcatalogItem.categories as CategoryData[]).find(
            (cat) => cat.slug === categorySlug
          )
        : undefined;
    if (categoryItem) {
      breadcrumbs.push({
        name: categoryItem.title,
        href: undefined,
        catalogItem: subcatalogItem,
      });
    }
  }

  return breadcrumbs;
};
